body {
  margin: 0;
  padding: 0;
  background-color: #18181B;
  color: #F2F4F5;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  box-sizing: border-box;
}

#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
}
.ToDoCounter{
    background-color: #232329;
    margin: 0;
    padding-left: 8px;
    padding-right: 8px;
}

.headCounter{
    padding-top: 24px;
}

.subCounter{
    font-size: 16px;
    padding-bottom: 24px;
    border-radius: 0 0 12px 12px;
    margin-bottom: 16px;
    color: #ED7448;
}

.ToDoCounter--loading{
    opacity: 25%;
}
.ToDoSearch {
    background: #232329;
    border-radius: 8px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 32px;
    width: calc(100% - 62px);
    font-size: 16px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: white;
  }
  
  .ToDoSearch::-webkit-input-placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .ToDoSearch:-ms-input-placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .ToDoSearch::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .ToDoSearch:focus {
    outline-color: #61DAFA;
  }

  .ToDoSearch:disabled {
    opacity: 25%;
  }
ul{
    margin: 0;
    padding: 0 0 56px 0;
    list-style: none;
}
.ToDoItem {
    background-color: #232329;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  }
  
  .ToDoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  .ToDoItem-p--complete {
    text-decoration: line-through;
  }
  
  .Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
  }
  
  .Icon-check {
    position: absolute;
    left: 12px;
  }
  .Icon-check--active {
    color: #4caf50;
  }
  
  .Icon-delete {
    position: absolute;
    top: -24px;
    right: 0;
  }
  .Icon-delete:hover {
    color: red;
  }
.CreateToDoButton {
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
  
    -webkit-transform: rotate(0);
  
            transform: rotate(0);
    transition: .3s ease;
    z-index: 1;
  }
  
  .CreateToDoButton:hover {
    -webkit-transform: rotate(224deg);
            transform: rotate(224deg);
  }
.ModalBackground {
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }
form {
    width: 90%;
    max-width: 300px;
    background-color: rgb(40, 40, 40);
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #f0f0f0;
    margin-bottom: 26px;
  }
  
  textarea {
    background-color: #f8f8f8;
    border: 2px solid #c5c5c5;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #353535;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
  }
  
  textarea::-webkit-input-placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  textarea:-ms-input-placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  textarea::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  textarea:focus {
    outline-color: #61DAFA;
  }
  
  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #ffffff;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
  }
  
  .TodoForm-button--add {
    background: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  }
  
  .TodoForm-button--cancel {
    background: transparent;
  }
.ToDosError{
    display: flex;
    flex-direction: column;
}

.errorIcon{
    margin-top: 24px;
    font-size: 100px;
    text-align: center;
}

.LoadingToDo-container{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32,35,41,0.15);
}

.LoadingToDo-text{
    margin: 24px 0 24px 24px;
    width: calc(100%-120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    opacity: 0;
}

.LoadingToDo-completeIcon, .LoadingToDo-deleteIcon{
    border-radius: 50px;
    height: 24px;
    width: 24px;
}

.LoadingToDo-completeIcon{
    position: absolute;
    left: 12px;
}

.LoadingToDo-deleteIcon{
    position: absolute;
    top: -12px;
    right: 12px;
}

.LoadingToDo-container,
.LoadingToDo-completeIcon,
.LoadingToDo-deleteIcon{
    background: linear-gradient(90deg, rgb(50, 50, 50), rgb(103, 102, 102));
    background-size: 400% 400%; 
    -webkit-animation: loadingAnimation 3s ease-in-out infinite; 
            animation: loadingAnimation 3s ease-in-out infinite;
}

@-webkit-keyframes loadingAnimation {
    0%{
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50%;
    }
}

@keyframes loadingAnimation {
    0%{
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50%;
    }
}
.EmptyToDos{
    display: flex;
    flex-direction: column;
}

.emptyIcon{
    margin-top: 24px;
    font-size: 100px;
    text-align: center;
}

.miniAddButton{
    color: #61DAFA;
}
.ChangeAlert-container {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #1e1e1f50;
    z-index: 2;
}

.ChangeAlert {
    position: fixed;
    bottom: -10px;
    left: -10px;
    right: -10px;
    padding: 34px;
    background-color: #1e1e1f;
    color: #f2f4f5;
    z-index: 2;
    text-align: center;
    font-size: 20px;
    font-size: 1.6rem;;
}

.ChangeAlert-container button {
    margin-top: 16px;
    margin-bottom: 16px;
    background: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    color: white;
    padding: 1rem;
    font-size: 1.6rem;
    border-radius: 0.8rem;
}
