.LoadingToDo-container{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32,35,41,0.15);
}

.LoadingToDo-text{
    margin: 24px 0 24px 24px;
    width: calc(100%-120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    opacity: 0;
}

.LoadingToDo-completeIcon, .LoadingToDo-deleteIcon{
    border-radius: 50px;
    height: 24px;
    width: 24px;
}

.LoadingToDo-completeIcon{
    position: absolute;
    left: 12px;
}

.LoadingToDo-deleteIcon{
    position: absolute;
    top: -12px;
    right: 12px;
}

.LoadingToDo-container,
.LoadingToDo-completeIcon,
.LoadingToDo-deleteIcon{
    background: linear-gradient(90deg, rgb(50, 50, 50), rgb(103, 102, 102));
    background-size: 400% 400%; 
    animation: loadingAnimation 3s ease-in-out infinite;
}

@keyframes loadingAnimation {
    0%{
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50%;
    }
}